import React from "react";

function About() {
  return (
    <div className="about">
      <div class="container">
        <div class="row align-items-center my-5">
          <div class="col-lg-7">
            <img
              
              src="images/iron4.jpg"   width="600px"
              
            />
          </div>
          <div class="col-lg-5">
          <h1><font color="Red">About</font></h1>
            <p>
            Iron Man is a superhero appearing in American comic books published by Marvel Comics. The character was co-created by writer and editor Stan Lee, developed by scripter Larry Lieber, and designed by artists Don Heck and Jack Kirby. The character made his first appearance in Tales of Suspense #39 (cover dated March 1963), and received his own title in Iron Man #1 (May 1968). Also in 1963, the character founded the Avengers alongside Thor, Ant-Man, Wasp and the Hulk.<br/>
            <a target="_blank" href="https://en.wikipedia.org/wiki/Iron_Man">Click here for more details</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default About;