import React from "react";

function Home({Charname,Actor,prod}) {
  return (
    <div className="home">
      <div class="container">
        <div class="row align-items-center my-5">
          <div class="col-lg-7">
            <img
              
              src="images/iron2.jpg" width="600px"
              
            />
          </div>
          <div class="col-lg-5">
          <h1><font color="Red">Home</font></h1>
          <h4>Character Name: <a target="_blank" href="https://en.wikipedia.org/wiki/Iron_Man_(2008_film)">{Charname}</a></h4>
            <h4>Actor Name: <a target="_blank" href="https://en.wikipedia.org/wiki/Robert_Downey_Jr.">{Actor}</a></h4>
            <h4>Production: <a target="_blank" href="https://www.marvel.com/movies">{prod}</a></h4>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;