import React from "react";

function Contact({name,id,section}) {
  return (
    <div className="contact">
      <div class="container">
        <div class="row align-items-center my-5">
          <div class="col-lg-7">
            <img
              
              src="images/iron3.jpg"  width="600px"
              
            />
          </div>
          <div class="col-lg-5">
            <h1><font color="Red">Contact</font></h1>
            
            <h4>NAME: {name}</h4>
            <h4>ID: {id}</h4>
            <h4>SECTION: {section}</h4>
            
          </div>
          
        </div>
        
      </div>
      
    </div>
  );
}

export default Contact;